.build-status {

    &__title {
        font-size: 32px;
        font-weight: 500;
        line-height: 40px;
        letter-spacing: -0.35px;
        color: #181E1A;
    }

    &__actions {
        display: flex;
        flex-direction: row;
        gap: 20px;
        padding-top: 32px;

        &-button {
            width: 190px;
            height: 44px;
            padding: 10px 16px;
            border-radius: 30px;
            background-color: #fff;
            border: 1px solid #7F9E56;
            color: #7F9E56;
            cursor: pointer;
        }

        .button-active {
            background-color: #527246;
            border: none;
            color: #fff;
        }

        #wes-town__button {
            width: 120px;
            height: 40px;
        }
    }

    &__slider {
        display: block;
        margin: 32px 0;
    }

    .slider-hidden {
        display: none;
    }
}

@media screen and (max-width: 992px) {
    .build-status {
        &__items {
            flex-direction: column;
            gap: 20px;
        }

        &__slider {
            margin: 65px 0;
        }
    }
}

@media screen and (max-width: 620px) { 
    .build-status__actions {
        flex-direction: column;
        align-items: start;
        gap: 16px;

        &-button {
            width: 100%;
        }

        #wes-town__button {
            width: 100%;
        }

    }

}

